<template>
  <div class="container">
    <div class="LoginBox">
      <div class="unLogin">
        <div class="doll"><img src="/img/doll.png" /></div>
        <h2>帳號登入</h2>
        <div class="formRow">
          <input
            type="text"
            placeholder="請輸入您的帳號"
            required
            v-model="User.Email"
          />
        </div>
        <div class="formRow">
          <input
            type="password"
            placeholder="請輸入您的密碼"
            required
            v-model="User.Password"
          />
        </div>
        <p class="otherLink">
          <router-link to="/register">註冊帳號</router-link
          ><router-link to="/forget">忘記密碼</router-link>
        </p>
        <div class="formRow">
          <div class="submitBtn">
            <a @click="login" href="javascript:void(0);">登入</a>
          </div>
        </div>
        <div class="formRow notice">
          <p>
            若有教育雲端帳號OpenID，請直接透過教育雲端帳號登入教師專區或學生專區即可
          </p>
        </div>
        <div class="openIDLink formRow">
          <a @click="oauth" href="javascript:void(0)"
            ><img src="/img/educloud.svg"
          /></a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      User: {
        Email: "",
        Password: "",
      },
    };
  },
  mounted() {
    this.setBase("login unlogin");
  },
  methods: {
    async login() {
      if (!this.validate(".LoginBox")) {
        return;
      }
      try {
        var result = await this.$api.login(this.User);
        this.$user.setUser(result);
        this.$api.setToken(result.t);
        this.$router.replace("/");
      } catch (e) {
        console.error(e);
        if (e.Status == 10) {
          alert("此帳號已被鎖定，請洽客服人員");
        } else if (e.Status == 6) {
          alert("您的密碼已經超過三個月沒有修改，請更新密碼後再登入");
          this.$router.replace("/fpassword");
        } else {
          alert("登入失敗，帳號或密碼錯誤");
        }
      }
    },
    async oauth() {
      var url = await this.$api.getOAuthUrl();
      window.location.href = url;
    },
  },
};
</script>